











































































































































































* {
  font-family: inherit;
  box-sizing: border-box; }

body {
  margin: 0;
  padding: 20px 0;
  font-family: Roboto, Noto Sans, -apple-system, BlinkMacSystemFont, sans-serif; }
button {
  border: none; }
button:focus {
  outline: none; }
h1 {
  text-align: center; }

.github {
  position: absolute;
  text-align: center;
  bottom: 15px;
  width: 100%; }

.game {
  padding-top: 40px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  max-width: 600px;
  margin: auto;
  user-select: none; }

.panel {
  >h3 {
    margin: 20px 0 10px; }
  >button {
    background-color: #6dabe8;
    padding: 10px 20px;
    font-size: 16px;
    text-transform: uppercase;
    color: #fff;
    border-radius: 6px;
    cursor: pointer;
    opacity: .6;
    transition: opacity .2s; }
  >button:hover {
    opacity: 1; } }
.panel__difficulty {
  display: flex;
  flex-direction: column; }


.circle {
  width: 300px;
  height: 300px;
  display: grid;
  grid-template-areas: 'a b' 'c d';
  >button {
    width: 100%;
    height: 100%;
    cursor: pointer;
    transition: border-color .1s, opacity .3s;
    opacity: .6; }
  >button:hover {
    border-color: #6e6e6e; }
  >button.glow {
    opacity: 1;
    box-shadow: 0 0 10px #fff;
    z-index: 2; } }

.circle__button[data-num="1"] {
  background-color: dodgerblue;
  border-top-left-radius: 100%;
  border-top: 2px solid transparent;
  border-left: 2px solid transparent; }
.circle__button[data-num="2"] {
  background-color: #FF5643;
  border-top-right-radius: 100%;
  border-top: 2px solid transparent;
  border-right: 2px solid transparent; }
.circle__button[data-num="3"] {
  background-color: #FEEF33;
  border-bottom-left-radius: 100%;
  border-bottom: 2px solid transparent;
  border-left: 2px solid transparent; }
.circle__button[data-num="4"] {
  background-color: #BEDE15;
  border-bottom-right-radius: 100%;
  border-bottom: 2px solid transparent;
  border-right: 2px solid transparent; }
@media (max-width: 425px) {
  .circle {
    width: 200px;
    height: 200px; } }
